<script>
export default {
  name: 'MenuItem',
  functional: true,
  props: {
    icon: {
      type: String,
      default: ''
    },
    activeIcon: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },
  render(h, context) {
    const { icon, title, activeIcon } = context.props
    const vnodes = []
    if (icon) {
      vnodes.push(<el-image fit="cover" src={ icon } style="width:19px;height:19px;margin-right:10px" />)
    }
    if (activeIcon) {
      vnodes.push(<el-image class='active' fit="cover" src={ activeIcon } style="width:19px;height:19px;margin-right:10px" />)
    }
    if (title) {
      vnodes.push(<span slot='title'>{(title)}</span>)
    }
    return vnodes
  }
}
</script>